import { Laya3D } from "../../../../Laya3D";
import { LayaEnv } from "../../../../LayaEnv";
import { EPhysicsCapable } from "../../../Physics3D/physicsEnum/EPhycisCapable";
import { Physics3DColliderShape } from "./Physics3DColliderShape";
export class CylinderColliderShape extends Physics3DColliderShape {
    get radius() {
        return this._radius;
    }
    set radius(value) {
        this._radius = value;
        if (LayaEnv.isPlaying)
            this._shape.setRadius(value);
    }
    get height() {
        return this._height;
    }
    set height(value) {
        this._height = value;
        if (LayaEnv.isPlaying)
            this._shape.setHeight(value);
    }
    get orientation() {
        return this._orientation;
    }
    set orientation(value) {
        this._orientation = value;
        if (LayaEnv.isPlaying)
            this._shape.setUpAxis(value);
    }
    constructor(radius = 0.5, height = 1.0, orientation = Physics3DColliderShape.SHAPEORIENTATION_UPY) {
        super();
        this._radius = 1;
        this._height = 0.5;
        this.radius = radius;
        this.height = height;
        this.orientation = orientation;
    }
    _createShape() {
        if (Laya3D.PhysicsCreateUtil.getPhysicsCapable(EPhysicsCapable.Physics_CylinderColliderShape)) {
            this._shape = Laya3D.PhysicsCreateUtil.createCylinderColliderShape();
        }
        else {
            console.error("CylinderColliderShape: cant enable CylinderColliderShape");
        }
    }
    clone() {
        var dest = new CylinderColliderShape(this._radius, this._height, this._orientation);
        this.cloneTo(dest);
        return dest;
    }
    cloneTo(destObject) {
        super.cloneTo(destObject);
        destObject.radius = this.radius;
        destObject.height = this.height;
        destObject.orientation = this.orientation;
    }
}
